// 匯入Modal，並建立modal實體,且宣告show與hide方法 只要mixins: [] 即可使用上述功能
import Modal from 'bootstrap/js/dist/modal'

export default {
  methods: {
    showModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    }
  },
  mounted () {
    this.modal = new Modal(this.$refs.modal)
  }
}
