// 後台產品頁面
<template>
 <div>
    <Loading :active="isLoading"></Loading>
    <div class="text-end">
      <button class="btn btn-primary mt-4" type="button" @click="openModal(true)">新增產品</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">分類</th>
          <th width="120">產品名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="100">是否啟用</th>
          <th width="200">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product in products" :key="product.id">
          <td>{{product.category}}</td>
          <td>{{product.title}}</td>
          <td class="text-right">
            {{product.origin_price}}
          </td>
          <td class="text-right">
            {{product.price}}
          </td>
          <td>
            <span class="text-success" v-if="product.is_enabled">啟用</span>
            <span class="text-muted" v-else>未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, product)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="openDeleteModal(product)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :pages="pagination" @emit-page="getProducts"></Pagination>
    <ProductModal ref="productModal" :product="tempProduct" @update-product="updateProduct"></ProductModal>
    <DeleteModal ref="deleteModal" :product="tempProduct" @delete-something="deleteProduct"></DeleteModal>
 </div>
</template>

<script>
import ProductModal from '../components/ProductModal.vue'
import DeleteModal from '../components/DeleteModal.vue'
import Pagination from '../components/Pagination.vue'

// import { currency } from '../methods/filters.js' 匯入方法，但目前使用全域方法，故不須匯入

export default {
  data () {
    return {
      products: [], // 存取api回傳的產品
      pagination: {}, // 存取api回傳的頁數
      tempProduct: {}, // props給modal
      isNew: false,
      isLoading: false
    }
  },
  components: {
    ProductModal,
    Pagination,
    DeleteModal
  },
  methods: {
    // currency, 匯入方法，但目前使用全域方法，故不須匯入
    // 取得產品列表
    getProducts (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products?page=${page}`
      // axios的$http方法
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            this.products = res.data.products
            this.pagination = res.data.pagination
            console.log(this.pagination)
          }
        })
    },
    openModal (isNew, product) {
      if (isNew) {
        this.tempProduct = {}
        // 修改titte
        const title = document.querySelector('.modal-title span')
        title.innerText = '新增產品'
      } else {
        // 編輯使用淺層複製
        this.tempProduct = { ...product }
        // 修改titte
        const title = document.querySelector('.modal-title span')
        title.innerText = '編輯產品'
      }
      this.isNew = isNew
      const productComponent = this.$refs.productModal
      productComponent.showModal()
    },
    openDeleteModal (product) {
      this.tempProduct = product
      const productComponent = this.$refs.deleteModal
      productComponent.showDeleteModal()
    },
    updateProduct (product) {
      // 將emit傳回的product存入tempProduct
      this.tempProduct = product
      // 取得.env中的VUE_APP_API並串接api
      // 新增(post)
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
      let httpMethod = 'post'
      const productComponent = this.$refs.productModal
      // 編輯(put)
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${product.id}`
        httpMethod = 'put'
      }
      // axios的$http方法
      this.$http[httpMethod](api, { data: this.tempProduct })
        .then((res) => {
          productComponent.hideModal()
          this.$pushMessageState(res, '產品更新')
          if (res.data.success) {
            // 獲取更新後的產品頁面
            this.getProducts()
            //  以下整合到pushMessageState.js
            //  透過mitt傳送成功訊息給toast
            //  this.emitter.emit('push-message', {
            //   style: 'success',
            //   title: '更新成功'
            // })
          }
          // else {
          //   // 透過mitt傳送失敗訊息給toast
          //   this.emitter.emit('push-message', {
          //     style: 'danger',
          //     title: '更新失敗',
          //     content: res.data.message.join('、')
          //   })
          // }
        })
    },
    //  刪除產品api驗證
    deleteProduct () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`
      // axios的$http方法
      this.$http.delete(api)
        .then((res) => {
          this.$pushMessageState(res, '產品刪除')
          if (res.data.success) {
            const delComponent = this.$refs.deleteModal
            delComponent.hideDeleteModal()
            this.getProducts()
          }
        })
    }
  },
  created () {
    this.getProducts()
  },
  inject: ['emitter']
}
</script>
