// 後台分頁元件 接受api回傳資料
<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li class="page-item" :class="{'disabled' : pages.current_page=== 1}" @click.prevent="updatePage(pages.current_page-1)">
        <a class="page-link" href="#" aria-label="Previous">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item"  v-for="page in pages.total_pages" :key="page" @click.prevent="updatePage(page)" :class="{'active' : page === pages.current_page}">
        <a class="page-link" href="#">
            {{page}}
        </a>
      </li>
      <li class="page-item" :class="{'disabled' : pages.current_page=== pages.total_pages}" @click.prevent="updatePage(pages.current_page+1)">
        <a class="page-link" href="#" aria-label="Next">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
// :pages="{ 頁碼資訊 }"
// @emitPages="更新頁面事件"
export default {
  // 使用Props取得pagination
  props: ['pages'],
  methods: {
    // 使用emit將當前頁傳給Product.vue
    updatePage (page) {
      this.$emit('emit-page', page)
    }
  }
}
</script>
